import React, { Component } from 'react';

class Header extends Component {
  render() {
    return (
      <div className='header'>
        <div className='logo'>
          <img src={require('../../img/kamakura_vivefaux.svg')}/>
        </div>
        {this.props.children}
      </div>
    );
  }
}

export default Header;



